import { useSelector } from 'react-redux';

export default function PofolList(){
    let data=useSelector(state=>state.pofol);

    return(
        <div className="pofolist">
            <ul>
                {data.map((d,i)=>
                    <li key={i+1}>
                        <div className="tit">
                            {d.makeup.map((d,i)=>
                                <p key={i+1}>{d}</p>
                            )}
                        </div>
                        <div className="desc">
                            <div className="imgbox">
                                <a href={d.href} target="_blank"><img src={"./assets/images/"+(d.liImg)} alt="sk zic  "/></a>
                            </div>
                            <a href={d.href}>{d.tit}</a>
                            <p>{d.p}</p>
                            <span>{d.desc}</span>
                        </div>
                    </li>
                )}
            </ul>
        </div>
    );
}