import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';

// import required modules
import { Autoplay } from 'swiper/modules';
import { useSelector } from 'react-redux';

export default function WorkSlide(){
    let data=useSelector(state => state.modalData);
    return(
        <Swiper
        slidesPerView={4}
        spaceBetween={30}
        speed={1200}
        loop={true}
        autoplay={{
            delay: 4500,
            disableOnInteraction: false
        }}
        modules={[Autoplay]}
        breakpoints={{
			1230: {
				slidesPerView: 3,
			},
			630: {
				slidesPerView: 2,
			},
			0: {
				slidesPerView: 1,
			},
		}}
        className="mySwiper"
        >
            {data.map((d,i)=>
                <SwiperSlide>
                    <div className="imgbox">
                        <a href="/"><img src={"./assets/images/"+(d.image)} alt={d.alt} /></a>
                    </div>
                    <span>{d.year}</span>
                    <p>{d.p}</p>
                </SwiperSlide>
            )}
        </Swiper>
    );
}