import { useSelector } from 'react-redux';

export default function SkillList(){
    let data=useSelector(state => state.skill);
    return(
        <>        
        {data.map((d,i)=>{
            return(
                <li key={i+1}>
                    <div className="tit">{d.tit}</div>
                    <div className="desc">
                        <strong>{d.strong}</strong>
                        <p>{d.p}</p>
                        <span>{d.span}</span>
                    </div>
                </li>
            );
        })}
        </>
    );
}