import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';

// import required modules
import { Pagination, Navigation } from 'swiper/modules';
import { useSelector } from 'react-redux';

export default function Page3(){
    let data=useSelector(state => state.history);
    return(
        <section id="page3">
            <div className="inner">
                <div className="title_area"><h2>HISTORY</h2></div>
                <div className="txt_area">
                    <Swiper
                    slidesPerView={1}
                    spaceBetween={30}
                    pagination={{
                        type: 'progressbar',
                    }}
                    speed={600}
                    breakpoints={{
                        1669:{
                            slidesPerView:3,
                            slidesPerGroup:3,
                        },
                        1230:{
                            slidesPerView:2,
                            slidesPerGroup:3,
                        },
                    }}
                    navigation={true}
                    modules={[Pagination, Navigation]}
                    className="mySwiper"
                    >
                        {data.map((d,i)=>
                            <SwiperSlide key={i+1}>
                                <span className="year">{d.year}</span>
                                <p className="tit">{d.tit}</p>
                                <ul>
                                    <Career props={d}/>
                                </ul>
                            </SwiperSlide>
                        )}
                    </Swiper>
                </div>
            </div>
        </section>
    );
}

function Career(data){
	let props=data.props;
	let pushArray=[];

	if(props.li !== undefined){
		pushArray=props.li.map((d, i) => <li key={i+1}>{d}</li>);
	}

	return(
		<>
			{pushArray}
		</>
	);
}