import React, { useRef, useState } from 'react';
import { useSelector } from 'react-redux';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation, Autoplay } from 'swiper/modules';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

import LogoList from "./LogoList";

export default function Page1(){
    let data=useSelector(state => state.pofol);
    return(
        <section id="page1">
			<Swiper
				slidesPerView={1}
                speed={1200}
                loop={true}
                autoplay={{
					delay: 4500,
					disableOnInteraction: false
				}}
				onSwiper={swiper => {
					let prevBtn=document.querySelector(".prev_btn");
                    let nextBtn=document.querySelector(".next_btn");

                    prevBtn.addEventListener("click", e =>{
                        swiper.slidePrev();
                    });
                    nextBtn.addEventListener("click", e =>{
                        swiper.slideNext();
                    });
				}}
				onSlideChange={swiper => {
					// console.log("slide change event");
					// console.log(swiper.activeIndex);
				}}
				modules={[Autoplay]}
				className="mySwiper"
			>
                {data.map((d,i)=>{
                    return(
                        <SwiperSlide key={i+1}>
                            <div className="innertxt">
                                <h2><a href={d.href}>{d.tit}</a></h2>
                                <p>{d.p}</p>
                                <span className="date">{d.date}</span>
                                <span className="tool">{d.tool}</span>
                            </div>
                            <a className="more" href={d.href}>MORE view</a>
                            <img src={"./assets/images/"+(d.keyImg)} alt={""+(i+1)} />
                        </SwiperSlide>
                    );
                })}
                <div className="prev_btn">
                    <span></span>
                    <span></span>
                    <p>PREV</p>
                </div>
                <div className="next_btn">
                    <span></span>
                    <span></span>
                    <p>NEXT</p>
                </div>
                <LogoList/>
            </Swiper>
        </section>
    );
}